'use client';
import React from 'react'
import { motion } from 'framer-motion';
import { AnimatedSection } from './AnimatedSection';
import Image from "next/image"
import { AnimatedCard } from './AnimatedCard';
import { CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { AnimatedBadge } from './AnimatedBadge';
import { Button } from "@/components/ui/button"
import { ExternalLink } from "lucide-react"
import Link from "next/link"
import { FaGithub, FaLinkedin } from "react-icons/fa";
import "keen-slider/keen-slider.min.css";
import { useKeenSlider } from "keen-slider/react";

export default function ProjectDetails() {
    const projects = [
        {
            title: "E-commerce Platform",
            description:
                "A full-stack e-commerce solution built with Next.js, Stripe, and PostgreSQL. Features include user authentication, payment processing, and admin dashboard.",
            tech: ["Next.js", "Stripe", "PostgreSQL"],
            image: "/placeholder.svg?height=200&width=400",
        },
        {
            title: "Task Management App",
            description:
                "A collaborative task management application with real-time updates, drag-and-drop functionality, and team collaboration features.",
            tech: ["React", "Socket.io", "MongoDB"],
            image: "/placeholder.svg?height=200&width=400",
        },
        {
            title: "Weather Dashboard",
            description:
                "A responsive weather dashboard that displays current weather conditions, forecasts, and interactive maps using external APIs.",
            tech: ["Vue.js", "Chart.js", "Weather API"],
            image: "/placeholder.svg?height=200&width=400",
        },
        {
            title: "Portfolio Website",
            description:
                "A personal portfolio website built with Next.js and Tailwind CSS, featuring animations and responsive design.",
            tech: ["Next.js", "Tailwind CSS", "Framer Motion"],
            image: "/placeholder.svg?height=200&width=400",
        },
        {
            title: "Portfolio Website",
            description:
                "A personal portfolio website built with Next.js and Tailwind CSS, featuring animations and responsive design.",
            tech: ["Next.js", "Tailwind CSS", "Framer Motion"],
            image: "/placeholder.svg?height=200&width=400",
        },
        // Add more projects as needed
    ];

    return (
        <div>
            <section id="projects" className="py-16 px-4 bg-muted/30">
                <div className='container mx-auto pt-[24px] pb-[24px] pl-[12px] pr-[12px] border-solid border-[1.5px] border-[#353941] rounded-[24px]'>
                    <div className='flex flex-row items-center justify-start pb-[12px] pt-[0px] gap-x-[12px]'>
                        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <circle cx="12" cy="12" r="12" fill="#353941" />
                        </svg>
                        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <circle cx="12" cy="12" r="12" fill="#353941" />
                        </svg>
                        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <circle cx="12" cy="12" r="12" fill="#353941" />
                        </svg>

                    </div>
                    <div className="container mx-auto pt-[40px] pb-[40px] bg-[#353941] rounded-[18px]">
                        <div className="max-w-full pl-[64px]">
                            <div className='flex flex-row items-center justify-between mb-[40px]'>
                                <AnimatedSection>
                                    <h2 className="text-[48px] md:text-[48px] font-bold text-white text-center mb-0">Projects</h2>
                                </AnimatedSection>
                                <AnimatedSection>
                                    <p className='max-w-[480px] text-base font-normal text-left text-white mb-0'>Explore some of my recent work that showcases my skills in web development, design, and problem-solving.</p>
                                </AnimatedSection>
                            </div>

                            <div
                                className="flex overflow-x-auto gap-x-[24px] pb-4 scrollbar-thin scrollbar-thumb-[#353941] scrollbar-track-[#23272f]"
                                style={{ scrollSnapType: 'x mandatory' }}
                            >
                                {projects.map((project, index) => (
                                    <div
                                        key={project.title}
                                        className="min-w-[328px] max-w-[400px] flex-shrink-0"
                                        style={{ scrollSnapAlign: 'start' }}
                                    >
                                        <AnimatedCard delay={index * 0.2} hoverScale={1.03}>
                                            <CardHeader>
                                                <CardTitle className="mb-2 text-white">{project.title}</CardTitle>
                                                <CardDescription className="mb-4">{project.description}</CardDescription>

                                            </CardHeader>
                                            <CardContent>
                                                <motion.div whileHover={{ scale: 1.05 }} transition={{ duration: 0.3 }}>
                                                    <Image
                                                        src={project.image || "/placeholder.svg"}
                                                        alt={project.title}
                                                        width={400}
                                                        height={200}
                                                        className="rounded-lg object-cover w-full"
                                                    />
                                                </motion.div>
                                                <div className="flex gap-2">
                                                    <motion.div whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}>
                                                        <Button variant="outline" size="sm" asChild>
                                                            <Link href="https://github.com" target="_blank" className='text-primary'>
                                                                <FaGithub className="h-4 w-4 mr-2 text-primary" />
                                                                Code
                                                            </Link>
                                                        </Button>
                                                    </motion.div>
                                                    <motion.div whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}>
                                                        <Button size="sm" asChild>
                                                            <Link href="https://example.com" target="_blank">
                                                                <ExternalLink className="h-4 w-4 mr-2" />
                                                                Live Demo
                                                            </Link>
                                                        </Button>
                                                    </motion.div>
                                                </div>
                                            </CardContent>
                                        </AnimatedCard>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>

            </section>
        </div>
    );
}

